"e has come when it will pay to act on the reality underlying the existing newspaper. The barriers down, i. e., all hindrances to the free movement of intelligence removed, through the completion of the machine for gathering and distributing news, (this machine.consisting of the printing press, the locomotive, the telegraph and their .belongings) the newspaper presents itself to us as a unified thingthe business of dealing in intelligence. In this way the journalist, hereafter the typical man of letters, comes to have a definite position in life independent of all other vocations, professions, or trades. He has a commodity of his ownthe truth. This discovery marks the appearance of a new commodity in commerce. A given thing functions, gains its proper status, as commodity in commerce when it may be dealt in profitably at its highest reality. From this point of view the need has been to study publicity as commodity, with a view to organizing -intelligence for the contemplated advance of the newspaper; it becomes clear that to move the daily newspaper forward is to strike at the base of the whole publishing structure. The sum of the publishing business at a given date is the amount of intelligence brought to the centre and- distributed. Thus regarded, the daily newspaper is about to become the leading .publishing interest, if it has not already reached the point. In contrast therewith the so-called book business is to be secondary or accessory. At least the daily newspaper holds the key to future development.Let me particularize further. Rightly understood, advances in the newspaper until now have been so many improvements in the physical machinery which the newspaper uses. Thus the London Times gained its advance through being first to carry into practical operation the cylinder printing press. The Walters had the lead in adapting steam to printing newspapers. This was in 1814. In those days and through the decades following the struggle was to secure and apply increased mechanical power. As opposed to this the newspaper has now at its service a perfect working machine. Under the machinery idea, as already indicated, are included also the locomotive and the telegraph. The advance movement rendered possible by this discovery must in the nature of things be the sum of all previous advances, being nothing less than a new ordering of intelligence. There comes in a change in the power of thoughta forward movement in consciousness. The need has been to set about organizing intelligence by the new light; this to compel a prime movement in literature with the daily newspaper as its centre of action."
